.modal {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
  padding: 0 15px;
}

.modal__close {
  position: absolute;
  width: 30px;
  height: 30px;
  background: #fff;
  border-radius: 50%;
  top: -60px;
  left: 50%;
  padding: 5px 11px;
  cursor: pointer;
  font-weight: 900;
}

.modal__box {
  position: relative;
  max-width: 450px;
  width: 100%;
  min-height: 100px;
  background: #fff;
  padding: 20px;
  text-align: justify;
  border-radius: 7px;
}

.modal__box span {
  display: inline-block;
  width: 20px;
}

