}

.card {
  background: #fff;
  -webkit-box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.1), -2px 0px 5px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.1), -2px 0px 5px 2px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  -webkit-transition: -webkit-box-shadow 0.3s;
  transition: -webkit-box-shadow 0.3s;
  transition: box-shadow 0.3s;
  transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;
  text-decoration: none;
  color: #222222;
}

.card:hover {
  -webkit-box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.1), -2px 0px 10px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.1), -2px 0px 10px 5px rgba(0, 0, 0, 0.1);
}

.card__img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.card__content {
  width: 100%;
  padding: 15px;
}

.card__title {
  display: block;
  font-size: 20px;
  margin-bottom: 20px;
}

.card__subtitle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.pages {
  margin: 20px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.pages__item {
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 2px;
  background: #fff;
  -webkit-box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.pages__item:hover {
  background: #fafafa;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.transition-loader {
  position: relative;
  height: 30px;
  width: 100%;
}

.transition-loader-inner {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
  position: absolute;
  width: 100%;
  padding: 0 100px;
  text-align: center;
}

.transition-loader-inner label {
  font-size: 1em;
  padding: 1%;
  opacity: 0;
  display: inline-block;
}

.transition-loader-inner label:after {
  font-family: FontAwesome;
  content: '';
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #5bbdff;
  display: block;
}

.transition-loader-inner label:nth-child(1) {
  -webkit-animation: loader 3s 600ms infinite ease-in-out;
  animation: loader 3s 600ms infinite ease-in-out;
}

.transition-loader-inner label:nth-child(2) {
  -webkit-animation: loader 3s 500ms infinite ease-in-out;
  animation: loader 3s 500ms infinite ease-in-out;
}

.transition-loader-inner label:nth-child(3) {
  -webkit-animation: loader 3s 400ms infinite ease-in-out;
  animation: loader 3s 400ms infinite ease-in-out;
}

.transition-loader-inner label:nth-child(4) {
  -webkit-animation: loader 3s 300ms infinite ease-in-out;
  animation: loader 3s 300ms infinite ease-in-out;
}

.transition-loader-inner label:nth-child(5) {
  -webkit-animation: loader 3s 200ms infinite ease-in-out;
  animation: loader 3s 200ms infinite ease-in-out;
}

.transition-loader-inner label:nth-child(6) {
  -webkit-animation: loader 3s 100ms infinite ease-in-out;
  animation: loader 3s 100ms infinite ease-in-out;
}

@keyframes loader {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-300px) scale(1);
            transform: translateX(-300px) scale(1);
  }
  33% {
    opacity: 1;
    -webkit-transform: translateX(0px) scale(2);
            transform: translateX(0px) scale(2);
  }
  66% {
    opacity: 1;
    -webkit-transform: translateX(0px) scale(1);
            transform: translateX(0px) scale(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(300px) scale(2);
            transform: translateX(300px) scale(2);
  }
}

@-webkit-keyframes loader {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-300px);
  }
  33% {
    opacity: 1;
    -webkit-transform: translateX(0px);
  }
  66% {
    opacity: 1;
    -webkit-transform: translateX(0px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(300px);
  }
}

.search {
  width: 100%;
  margin: 10px 0;
}

.search__input {
  width: calc( 100% - 100px);
  border: none;
  border-bottom: 1px solid #5faafff8;
  outline: none;
  -webkit-transition: .2s;
  transition: .2s;
  margin-bottom: 2px;
  padding: 3px 10px;
  font-size: 20px;
}

.search__input:focus {
  border-bottom: 3px solid #3e97fdf8;
  margin-bottom: 0px;
}

.search__button {
  width: 90px;
  height: 30px;
  margin-left: 5px;
  border: none;
  background: #64c6ff;
  font-size: 18px;
  border-radius: 3px;
  -webkit-box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -webkit-transition: .1s;
  transition: .1s;
}

.search__button:hover {
  -webkit-box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.4);
          box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.4);
}

.search__button:active {
